$yellow: #ffc800;
$dark-bg: #1a1a1a;
$card-bg: #2a2a2a;
$text-white: #ffffff;
$text-secondary: #cccccc;

/* Подарок в левом нижнем углу */
.gift-button {
  position: fixed;
  bottom: 30px;
  left: 30px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: linear-gradient(135deg, $yellow 0%, #ffb300 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 100;
  box-shadow: 0 8px 25px rgba($yellow, 0.3);
  transition: all 0.3s ease;

  .gift-emoji {
    font-size: 36px;
    animation: shake 0.5s ease-in-out infinite;
    animation-delay: 2s;
  }

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 12px 35px rgba($yellow, 0.5);
  }

  @media (max-width: 768px) {
    bottom: 20px;
    left: 20px;
    width: 60px;
    height: 60px;

    .gift-emoji {
      font-size: 28px;
    }
  }
}

/* Анимация подергивания */
@keyframes shake {
  0%, 100% {
    transform: scale(1) rotate(0deg);
  }
  25% {
    transform: scale(1.05) rotate(-5deg);
  }
  50% {
    transform: scale(1.1) rotate(5deg);
  }
  75% {
    transform: scale(1.05) rotate(-5deg);
  }
}

/* Оверлей для модального окна */
.gift-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Содержимое модального окна */
.gift-modal-content {
  position: relative;
  background: linear-gradient(135deg, $card-bg 0%, #1f1f1f 100%);
  border: 2px solid $yellow;
  border-radius: 20px;
  padding: 0;
  max-width: 500px;
  width: 90%;
  animation: slideUp 0.4s ease;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.8);

  @media (max-width: 768px) {
    max-width: 90%;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Кнопка закрытия */
.gift-modal-close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 35px;
  height: 35px;
  border: none;
  background: rgba($yellow, 0.1);
  color: $yellow;
  border-radius: 50%;
  cursor: pointer;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  z-index: 1001;

  &:hover {
    background: $yellow;
    color: $dark-bg;
  }
}

/* Внутреннее содержимое модального окна */
.gift-modal-inner {
  padding: 40px 30px;
  text-align: center;
  position: relative;

  @media (max-width: 768px) {
    padding: 30px 20px;
  }
}

/* Декорация */
.gift-decoration {
  font-size: 48px;
  margin-bottom: 15px;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* Заголовок модального окна */
.gift-modal-title {
  font-size: 32px;
  color: $yellow;
  margin: 0 0 25px 0;
  font-weight: 700;
  text-shadow: 0 2px 8px rgba($yellow, 0.2);

  @media (max-width: 768px) {
    font-size: 24px;
  }
}

/* Сравнение цен */
.gift-modal-prices {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin: 30px 0;
  padding: 25px;
  background: rgba($yellow, 0.05);
  border-radius: 15px;
  border: 1px solid rgba($yellow, 0.2);

  @media (max-width: 768px) {
    gap: 15px;
    margin: 20px 0;
    padding: 20px;
  }
}

.price-old {
  display: flex;
  flex-direction: column;
  align-items: center;

  .label {
    font-size: 13px;
    color: #888;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 5px;
  }

  .amount {
    font-size: 24px;
    color: #888;
    text-decoration: line-through;
    font-weight: 600;

    @media (max-width: 768px) {
      font-size: 20px;
    }
  }
}

.price-divider {
  font-size: 28px;
  color: $yellow;
  font-weight: 700;
}

.price-new {
  display: flex;
  flex-direction: column;
  align-items: center;

  .label {
    font-size: 13px;
    color: $yellow;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 5px;
    font-weight: 600;
  }

  .amount {
    font-size: 32px;
    color: $yellow;
    font-weight: 700;

    @media (max-width: 768px) {
      font-size: 26px;
    }
  }
}

/* Текст предложения */
.gift-modal-text {
  color: $text-secondary;
  font-size: 16px;
  line-height: 1.6;
  margin: 20px 0;
  padding: 20px;
  background: rgba($yellow, 0.02);
  border-left: 3px solid $yellow;
  border-radius: 8px;

  strong {
    color: $yellow;
    font-weight: 700;
  }

  .highlight {
    display: block;
    color: $yellow;
    font-weight: 600;
    margin-top: 12px;
    font-size: 17px;
  }

  @media (max-width: 768px) {
    font-size: 14px;
    padding: 15px;
  }
}

/* Таймер */
.gift-modal-timer {
  margin: 20px 0;
  padding: 15px;
  background: rgba(255, 200, 0, 0.1);
  border-radius: 10px;

  p {
    color: $yellow;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    animation: pulse 2s ease-in-out infinite;
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

/* Кнопка покупки */
.btn-buy {
  margin-top: 30px;
  font-size: 18px;
  font-weight: 700;
  padding: 18px 40px;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 30px rgba($yellow, 0.3);
  }

  @media (max-width: 768px) {
    font-size: 16px;
    padding: 16px 30px;
  }
}

/* Гарантия */
.gift-modal-guarantee {
  color: #888;
  font-size: 13px;
  margin: 20px 0 0 0;
  letter-spacing: 0.5px;

  @media (max-width: 768px) {
    font-size: 12px;
  }
}
